@import "normalize";
@import "config";

* {
  box-sizing: border-box;
}

body {
  background: $primary-color;
  color: set-text-color($primary-color);
  height: 100%;
  margin: 0;
  font-family: "Work Sans", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
}

a {
  color: #fff;
  text-decoration: none;
  transition: all 320ms ease;
}

// Headings
h1,
h2,
h3 {
  margin: 0;
  font-weight: 400;
}



.break {
  display: none;
}

.text-secondary {
  color: $secondary-color;
}

//Header
.header {
  height: 100vh;
  padding: 1rem 4rem;
  background: url(../img/background.jpeg) no-repeat center center;
  background-attachment: fixed;
  background-size: cover;

  &-logo {
    margin-top: 4rem;
    margin-left: 20%;
    margin-right: -15rem;
    display: inline-block;
    .portrait {
      width: 400px;
      height: 400px;
      background: url('../img/portrait.png');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  .lg-heading {
    margin-top: 20vh;
    margin-bottom: 5vh;
    font-size: 6rem;
    display: inline-block;
    text-shadow: 0px 1px 3px rgba(0, 0, 0, 1), 0px 1px 15px rgba(0, 0, 0, 1);
  }

  .sm-heading {
    margin-bottom: 2rem;
    padding: 0.2rem 1rem;
    background: rgba(lighten($primary-color, 2), 0.5);
    text-shadow: 0px 1px 15px rgba(0, 0, 0, 1);
    text-align: center;
  }

  .icons {
    text-align: center;
    font-size: 32px;
    a {
      padding: 1rem;
      text-shadow: 0px 1px 10px rgba(0, 0, 0, 1);
      
      &:hover {
        color: $secondary-color;
        text-shadow: 0px 1px 3px rgba(0, 0, 0, 1);
        @include easeOut();
      }
    }
  }
}

//Main
.main {
  background-color: #fff;
  box-shadow: 0 -5px 5px -5px #333;
  padding: 3rem;
  //height: 100%;
  min-height: calc(100vh - 60px); //Item above footer - footer height!
}

//Projects

.projects-heading {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  .projects-square {
    box-shadow: 0 -5px 5px -5px #333, -5px 0 5px -5px #333;
    position: relative;
    height: 150px;
    width: 150px;
    background-color: #fff;
    transform: rotate(45deg);
    bottom: -75px;
  }
  .projects-title {
    text-align: center;
    color: #000;
    position: absolute;
    z-index: 5;
    top: 75px;
    left: 50%;
    transform: translateX(-50%); 
    cursor: pointer;   
  }
}
.projects {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  .item {
    background-color: #f5f5f5;
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 5vh;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.5);
    &-box {
      color: #333;
      display: block;
      padding: 0.5rem 1rem;
      border: 0;
      margin-bottom: 0.3rem;
    }
    &-title {
      color: #000;
      margin: 0 0 10px;
      font-weight: 600;
      font-size: 1.2rem;
    }
    &-desc {
      margin: 0;
      line-height: 1.2rem;
      height: 48px;
    }
    &-tech {
      margin: 1rem;
      height: 91px;
      text-align: center;
    }
    .tag {
      border-radius: 3px;
      color: #fff;
      display: inline-block;
      height: 26px;
      line-height: 26px;
      padding: 0 5px 0 5px;
      margin: 5px 0;
      text-decoration: none;
      text-shadow: 1px 1px 1px #000;
      box-shadow: 0px 0px 1px 0px rgba(0,0,0,0.75);
      font-size: 16px;
      background: #e14a4d;
    }
    &-buttons {
      display: flex;
      justify-content: center;
      align-items: center;
      .btn {
        display: inline-block;
        padding: 0.5rem 1rem;
        margin: 0 1rem;
        border: 0;
        width: 40%;
        border-radius: 5px;
        white-space: nowrap;
        text-align: center;
        &-dark {
          @extend .btn;
          background: #000;
          color: #fff;
          box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.5);
        }
        &-light {
          @extend .btn;
          background: #fff;
          color: #000;
          box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.75);
        }
        &:hover {
          background: $secondary-color;
          color: set-text-color($secondary-color);
        }
      }
    }
  }

  img {
    width: 100%;
    border-radius: 5px;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.5);
    &:hover {
      opacity: 0.5;
      border-color: $secondary-color;
      @include easeOut();
    }
  }
}


//Other stuff
.other {
  .knowledge {
  
    &_button {
      display: block;
      max-width: 300px;
      height: 65px;
      text-align: center;
      margin: 0 auto;
      font-size: 1.4rem;
      padding: 1rem;
      margin-bottom: 3rem;
      cursor: pointer;
      background: $secondary-color;
      box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.5);
      &:hover {
        box-shadow: 0px 3px 6px 0px rgba(0,0,0,0.9);
      }
      a {
        color: #000;
      }
    }
  }
  margin-top: 20vh;
  &-heading {
    color: #000;
    font-size: 2rem;
    text-align: center;
  }
  &-stuff {
    max-width: 300px;
    margin: 2rem auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    a {
      color: #000;
      background-color: #CAEBF2;
      display: block;
      margin: 5px;
      padding: 5px;
      border: 1px solid #000;
      border-radius: 2px;
      &:hover {
        box-shadow: 1px 2px 1px 0px rgba(0,0,0,0.75);
      }
    }
  }
}

//Footer

.footer {
  text-align: center;
  padding-top: 1rem;
  background: darken($primary-color, 10);
  color: set-text-color($primary-color);
  height: auto;
  
  a {
    &:hover {
      text-decoration: underline;
    }
  }
    i {
      color: $secondary-color;
      margin-right: 5px;
    }

    &-copy {
      font-size: 0.7rem;
    }
}

//Media querries
@import "mobile";