//Media Querries
//Extra Large
@include mediaXl {
    .projects {
        grid-template-columns: repeat(4, 1fr);
    }
}

//Large
@include mediaLg {
    .header {
        padding-top: 2rem;
        background-attachment: scroll;
        &-logo {
            display: block;
            margin-top: 2rem;
            width: 50vw;
            display: inline-block;
            .portrait {
              width: 100%;
              height: 40vh;
            }    
          }
        .lg-heading {
            margin-top: 0;
            display: block;
            font-size: 4rem;
            text-align: center;
            margin-top: -5vh;
        }
        .sm-heading {
            font-size: 2rem;
            text-align: center;
        }
        .icons {
            width: auto;
            font-size: 2rem;
            text-align: center;
            margin-top: 4vh;
        }

    }
    .main {
        padding: 5rem;
        .projects {
            grid-template-columns: repeat(3, 1fr);
            .item {
                &-desc {
                    height: auto;
                }
                &-tech {
                    height: auto;
                }
            }
        }
    }
}

//Medium
@include mediaMd {
    .header {
        padding-top: 6rem 2rem;
        background-attachment: scroll;
        &-logo {
            display: block;
            margin-top: 4rem;
            width: 50vw;
            .portrait {
              width: 100%;
              height: 40vh;
            }    
          }
        .lg-heading {
            margin-top: 0;
            display: block;
            font-size: 10vw;
            text-align: center;
            margin-top: -5vh;
        }
        .sm-heading {
            font-size: 6vw;
            text-align: center;
        }
        .icons {
            font-size: 2rem;
            text-align: center;
            margin-top: 4vh;
        }

    }
    .main {
        padding: 8rem;
        .projects {
            grid-template-columns: 1fr;
            .item {
                &-desc {
                    height: auto;
                }
                &-tech {
                    height: auto;
                }
            }
        }
    }
}

//Small
@include mediaSm {
    .header {
        padding: 4rem 2rem;
        background-attachment: scroll;
        &-container {
            margin-top: 0;
            height: calc(100% - 30px);
        }
        &-logo {
            display: block;
            width: 50vw;
            margin-top: 0;
            .portrait {
                width: 100%;
                height: 40vh;
              }
        }
        .lg-heading {
            line-height: 2rem;
            margin-bottom: 1rem;
        }
        .icons {
            font-size: 6vw;
            width: 100%;
        }
    }
    .main {
        padding: 4rem 1rem;
    }
    .projects-heading {
        position: static;
        left: 0;
        transform: translateX(0);
        .projects-square {
          box-shadow: 0 -5px 5px -5px #333, -5px 0 5px -5px #333;
          position: relative;
          height: 100px;
          width: 100px;
          background-color: #fff;
          transform: rotate(45deg);
          bottom: -50px;
          margin: 0 auto;
        }
        .projects-title {
          text-align: center;
          font-size: 1.2rem;
          color: #000;
          position: absolute;
          z-index: 5;
          top: 45px;
          left: 50%;
        }
      }
}